@import 'https://fonts.googleapis.com/css?family=Share+Tech+Mono';
@font-face {
  font-family: 'Digital';
  src: url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot');
  src: url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.eot?#iefix')
      format('embedded-opentype'),
    url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff2')
      format('woff2'),
    url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.woff')
      format('woff'),
    url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.ttf')
      format('truetype'),
    url('//db.onlinewebfonts.com/t/8e22783d707ad140bffe18b2a3812529.svg#Digital-7')
      format('svg');
}
body {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  background-color: gray;
  text-align: center;
  display: grid;
  grid-template-columns: 1fr;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root{
  margin-left: 30%;
  height: 50vh;
  width:40%;
  border-style: double;
  border-color: black;
}
.display{
  background-color: black;
  display: grid;
  height: 20%px;
  border-style: groove;
  text-align: right;
  grid-template: 1fr;
}
.keys{
  height: 80%;
  gap: 5px;
  padding: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
.keys button:first-child {
  color: #ffffff;
  grid-column-start: 1;
  grid-column-end: 3;
  background-color: #561a56;
}

.keys button:nth-child(15) {
  color: #ffffff;
  grid-row: 4 / 6;
  grid-column: 4 / 4;
  background-color: green;
}
.keys button:nth-child(16) {
  grid-column-start: 1;
  grid-column-end: 3;
}
button{
  border-radius: 10%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#Calculator{
  background-color: rgb(75, 75, 75);
  height: 100%;
}
.output{
  font-family:Digital;
  font-size: 20px;
  color: orange;
}
.input{
  font-family: Digital;
  font-size: 20px;
  color: white;

}
